      <div class="event-main-image">
        <img v-if="mainImage" :src="mainImage" alt="ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ" />
        <div v-else class="no-image">Ïù¥ÎØ∏ÏßÄ ÏóÜÏùå</div>
      </div>

      <div class="meta-card improved-meta">
        <h5 class="meta-card-title">Î©îÏù∏ Ïù¥Î≤§Ìä∏ Ï†ïÎ≥¥</h5>
        <div class="info-grid">
          <div class="info-label">Ïù¥Î≤§Ìä∏Î™Ö</div>
          <div class="info-value">{{ event.event_name }}</div>

          <div class="info-label">Í∏∞Í¥ÄÎ™Ö</div>
          <div class="info-value">{{ event.org_name }}</div>

          <div class="info-label">Îß§ÎãàÏ†Ä</div>
          <div class="info-value">{{ event.main_manager_name }}</div>

          <div class="info-label">Ïû•ÏÜå</div>
          <div class="info-value">{{ event.event_location }}</div>

          <div class="info-label">ÏµúÎåÄ Ï∞∏Ïó¨Ïûê</div>
          <div class="info-value">{{ event.max_participants }}</div>

          <div class="info-label">Î™®Ïßë Í∏∞Í∞Ñ</div>
          <div class="info-value">
            {{ formatDate(event.recruit_start_date) }} ~
            {{ formatDate(event.recruit_end_date) }}
          </div>

          <div class="info-label">ÏßÑÌñâ Í∏∞Í∞Ñ</div>
          <div class="info-value">
            {{ formatDate(event.event_start_date) }} ~
            {{ formatDate(event.event_end_date) }}
          </div>
        </div>
      </div>

      <div class="content-block">
        <div class="content-title">Ïù¥Î≤§Ìä∏ ÎÇ¥Ïö©</div>
        <div class="content-box whitespace-pre-line">
          {{ event.event_content }}
        </div>
      </div>

      <div v-if="subEventApplies.length" class="detail-card sub-event-card">
        <h2 class="text-xl font-semibold mb-4">Ïã†Ï≤≠Ìïú ÏÑ∏Î∂Ä Ïù¥Î≤§Ìä∏ Ï†ïÎ≥¥</h2>

        <table class="sub-event-table">
          <thead>
            <tr>
              <th>ÏÑ∏Î∂ÄÏù¥Î≤§Ìä∏ÏΩîÎìú</th>
              <th>ÏÑ∏Î∂ÄÏù¥Î≤§Ìä∏Î™Ö</th>
              <th>ÏãúÌñâÍ∏∞Í∞Ñ</th>
              <th>Î™®ÏßëÏù∏Ïõê</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="sub in subEventApplies" :key="sub.sub_event_code">
              <td>{{ sub.sub_event_code }}</td>
              <td>{{ sub.sub_event_name }}</td>
              <td>
                {{ formatDate(sub.sub_event_start_date) }} ~
                {{ formatDate(sub.sub_event_end_date) }}
              </td>
              <td>{{ sub.sub_recruit_count }}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="meta-card">
        <h5>ÏûêÎÖÄ Ï†ïÎ≥¥</h5>
        <div class="info-grid child-info-grid">
          <div class="info-label">ÏûêÎÖÄÎ™Ö</div>
          <div class="info-value">{{ applyInfo.child_name }}</div>

          <div class="info-label">ÏÑ±Î≥Ñ</div>
          <div class="info-value">{{ applyInfo.child_gender_name }}</div>

          <div class="info-label">Ï£ºÎØºÎ≤àÌò∏</div>
          <div class="info-value">{{ applyInfo.child_ssn }}</div>
        </div>
      </div>

      <div class="meta-card">
        <h5>Ïã†Ï≤≠Ïûê Ï†ïÎ≥¥</h5>
        <div class="info-grid applicant-info-grid">
          <div class="info-label">Ïã†Ï≤≠ÏûêÎ™Ö</div>
          <div class="info-value">{{ applyInfo.applicant_name }}</div>

          <div class="info-label">Ï£ºÏÜå</div>
          <div class="info-value">{{ applyInfo.applicant_address }}</div>

          <div class="info-label">ÏÜåÏÜçÍ∏∞Í¥Ä</div>
          <div class="info-value">{{ applyInfo.applicant_org_name }}</div>

          <div class="info-label">Ï£ºÎØºÎ≤àÌò∏</div>
          <div class="info-value">{{ applyInfo.applicant_ssn }}</div>

          <div class="info-label">Ï†ÑÌôîÎ≤àÌò∏</div>
          <div class="info-value">{{ applyInfo.applicant_phone }}</div>

          <div class="info-label">Ïù¥Î©îÏùº</div>
          <div class="info-value">{{ applyInfo.applicant_email }}</div>
        </div>
      </div>

      <div class="block-card">
        <div class="field-block">
          <div class="field-label">Ï≤®Î∂ÄÌååÏùº</div>

          <div v-if="event.attachments?.length">
            <ul class="file-list">
              <li v-for="file in event.attachments" :key="file.server_filename">
                <span class="file-name" @click="previewFile(file)">
                  {{ file.original_filename }}
                </span>
              </li>
            </ul>
          </div>
          <div v-else class="text-xs text-gray-400">
            Ï≤®Î∂ÄÎêú ÌååÏùºÏù¥ ÏóÜÏäµÎãàÎã§.
          </div>
        </div>

        <div v-if="previewImage" class="preview-modal" @click="closePreview">
          <img :src="previewImage" class="preview-img" />
        </div>
      </div>
    </div>

    <div class="pt-4 border-t mt-2 space-y-3" v-if="isManager">
      <div class="approve-actions">
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleApprove"
        >
          ÏäπÏù∏
        </MaterialButton>
        <MaterialButton
          color="dark"
          size="sm"
          class="px-4"
          @click="handleReject"
        >
          Ï∑®ÏÜå
        </MaterialButton>
      </div>
    </div>
  </section>
</template>

<script setup>
// Í∏∞Îä• Î°úÏßÅÏùÄ Î≥ÄÍ≤ΩÌïòÏßÄ ÏïäÏäµÎãàÎã§.
import { ref, onMounted, nextTick, computed } from "vue";
import { useRoute, useRouter } from "vue-router";
import axios from "axios";
import MaterialButton from "@/components/MaterialButton.vue";

const route = useRoute();
const router = useRouter();

// Î°úÍ∑∏Ïù∏ Í∂åÌïú
const getLoginRole = () => {
  const userStr = localStorage.getItem("user");
  if (!userStr) return null;
  try {
    const data = JSON.parse(userStr);
    return data.role || null;
  } catch {
    return null;
  }
};

const loginRole = ref(getLoginRole());

const applyInfo = ref({});

const isManager = computed(() => {
  // Î°úÍ∑∏Ïù∏ Í∂åÌïúÏù¥ AA2ÏùºÎïåÎßå Î≤ÑÌäº Î≥¥Ïó¨Ï£ºÍ∏∞
  return loginRole.value === "AA2";
});

const applyCode = Number(route.params.applyCode || 0);
const eventCode = Number(route.params.eventCode || 0);

const event = ref({
  sub_events: [],
  attachments: [],
});

const mainImage = ref("");
const isApplied = ref(false);

// ÎØ∏Î¶¨Î≥¥Í∏∞ Ïù¥ÎØ∏ÏßÄ
const previewImage = ref("");

// ÌååÏùº ÎØ∏Î¶¨Î≥¥Í∏∞
const previewFile = (file) => {
  const ext = file.original_filename.split(".").pop().toLowerCase();

  // Ïù¥ÎØ∏ÏßÄÎ©¥ Î™®Îã¨Î°ú Î≥¥Í∏∞
  if (["jpg", "jpeg", "png", "gif", "webp"].includes(ext)) {
    previewImage.value = file.file_path;
    return;
  }

  // PDFÎ©¥ ÏÉà Ï∞Ω ÎØ∏Î¶¨Î≥¥Í∏∞
  if (ext === "pdf") {
    window.open(file.file_path, "_blank");
    return;
  }

  // Í∑∏ Ïô∏ ÌååÏùºÏùÄ Îã§Ïö¥Î°úÎìú
  window.location.href = file.file_path;
};

const closePreview = () => {
  previewImage.value = "";
};

// ÎÇ†Ïßú Ìè¨Îß∑
const formatDate = (d) => (d ? new Date(d).toISOString().slice(0, 10) : "");

// Î°úÍ∑∏Ïù∏ Ïú†Ï†Ä ÏΩîÎìú
const getLoginUserCode = () => {
  const userStr = localStorage.getItem("user");
  if (!userStr) return null;
  try {
    const data = JSON.parse(userStr);
    return data.user_code || null;
  } catch {
    return null;
  }
};

// Ïù¥Î≤§Ìä∏ Ï°∞Ìöå
const fetchEvent = async () => {
  try {
    const userCode = getLoginUserCode();
    const res = await axios.get(`/api/event/${eventCode}`, {
      params: { user_code: userCode },
    });
    event.value = res.data.data || {};
    isApplied.value = !!event.value.alreadyApplied;

    // ÎåÄÌëú Ïù¥ÎØ∏ÏßÄ
    const img = event.value.attachments.find((x) =>
      /\.(jpg|jpeg|png|gif)$/i.test(x.original_filename)
    );
    mainImage.value = img ? img.file_path : "";

    await nextTick();
  } catch (err) {
    console.error("fetchEvent error:", err);
  }
};

const subEventApplies = ref([]);
const fetchMySubEventApplies = async () => {
  try {
    const userCode = getLoginUserCode();
    if (!userCode) {
      subEventApplies.value = [];
      return;
    }

    const res = await axios.get("/api/event/applyList", {
      params: { user_code: userCode },
    });

    const allApplies = res.data.data || [];

    // DD2 ÏÑ∏Î∂Ä Ïù¥Î≤§Ìä∏Ïù¥Î©¥ÏÑú ÌòÑÏû¨ Ïù¥Î≤§Ìä∏ ÏΩîÎìúÏóê Ìï¥ÎãπÌïòÎäî Í≤ÉÎßå ÌïÑÌÑ∞ÎßÅ
    subEventApplies.value = allApplies.filter(
      (item) =>
        item.apply_type === "DD2" && Number(item.event_code) === eventCode
    );

    console.log("Ïã†Ï≤≠Ìïú ÏÑ∏Î∂Ä Ïù¥Î≤§Ìä∏:", subEventApplies.value);
  } catch (err) {
    console.error("Ïã†Ï≤≠Ìïú ÏÑ∏Î∂Ä Ïù¥Î≤§Ìä∏ Ï°∞Ìöå Ïã§Ìå®:", err);
    subEventApplies.value = [];
  }
};

// ÏÉÅÌÉú Pill ÌÅ¥ÎûòÏä§
const statusClass = (status) => {
  switch (status) {
    case "DE1":
      return "status-pill--before";
    case "DE2":
      return "status-pill--done";
    case "DE4":
      return "status-pill--rejected";
    default:
      return "";
  }
};

// Ïã†Ï≤≠Ïûê/ÏûêÎÖÄ Ï°∞Ìöå
const fetchApply = async () => {
  try {
    const res = await axios.get(`/api/event/attendanceList/${applyCode}`);
    applyInfo.value = res.data.data || {};

    await nextTick();
  } catch (err) {
    console.error("fetchApply error:", err);
  }
};

// ÏäπÏù∏ (Îã¥ÎãπÏûê)
const handleApprove = async () => {
  try {
    const res = await axios.post(`/api/event/myApply/approve/${applyCode}`);
    if (res.data.success) {
      alert("ÏäπÏù∏ÎêòÏóàÏäµÎãàÎã§.");

      await fetchEvent();
      await fetchApply();
      console.log("applyInfo after approve:", applyInfo.value);
    } else {
      alert(res.data.message || "ÏäπÏù∏ Ïã§Ìå®");
    }
  } catch (err) {
    console.error(err);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (err.message || ""));
  }
};

// Ï∑®ÏÜå (Îã¥ÎãπÏûê)
const handleReject = async () => {
  try {
    const res = await axios.post(`/api/event/myApply/reject/${applyCode}`);
    if (res.data.success) {
      alert("Ï∑®ÏÜå Ï≤òÎ¶¨ÎêòÏóàÏäµÎãàÎã§.");

      await fetchEvent();
      await fetchApply();
    } else {
      alert(res.data.message || "Ï∑®ÏÜå Ïã§Ìå®");
    }
  } catch (err) {
    console.error(err);
    alert("ÏÑúÎ≤Ñ Ïò§Î•ò: " + (err.message || ""));
  }
};

// ÌôîÎ©¥ Ïù¥Îèô
const goBack = () => router.back();

onMounted(async () => {
  await fetchEvent();
  await fetchApply();
  await fetchMySubEventApplies();
});
</script>

<style scoped>
