  padding: 1rem 1rem 0 1rem;
  color: #111827;
}

/* =============================
   üìå Ï†ïÎ≥¥ Í∑∏Î¶¨Îìú Ïä§ÌÉÄÏùº (Í∞úÏÑ†Îêú Í∞ÄÎèÖÏÑ±)
   ============================= */
.info-grid {
