.info-grid > div:nth-last-child(2),
.info-grid > div:last-child {
  border-bottom: none;
}

/* ============================
   üìå Ïù¥Î≤§Ìä∏ ÎÇ¥Ïö© Í∞ÄÎèÖÏÑ± Î∞ïÏä§
   ============================ */
.content-block {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.content-title {
  font-size: 0.95rem;
